* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.flatpickr-calendar {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.flatpickr-day {
  color: #333;
  background-color: rgba(0, 0, 0, 0);
}

.flatpickr-day:hover {
  background-color: #f0f0f0;
}

.flatpickr-monthDropdown-months, .flatpickr-yearDropdown-years, .flatpickr-hour, .flatpickr-minute {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
}

.flatpickr-prev-month, .flatpickr-next-month, .flatpickr-prev-year, .flatpickr-next-year {
  color: #333;
  background-color: rgba(0, 0, 0, 0);
}

.flatpickr-prev-month:hover, .flatpickr-next-month:hover, .flatpickr-prev-year:hover, .flatpickr-next-year:hover {
  background-color: #f0f0f0;
}

.flatpickr-todayButton {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-top: none;
}

.flatpickr-todayButton:hover {
  background-color: #e0e0e0;
}

.flatpickr-clear {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
}

.flatpickr-clear:hover {
  background-color: #e0e0e0;
}

